import CustomCheckbox from "../../common/customCheckbox/CustomCheckbox";
import FilterBlock from './../../common/filterWrapBlock/FilterWrapBlock';
import FilterToggle from "../../common/filterToggle/FilterToggle";

import './filterExperience.scss';

const FilterExperience = () => {

    const experienceBlock = [
        { clazz: 'experience', title: ' ', name: 'ex1', num: '603' },
        { clazz: 'experience', title: ' 1 ', name: 'ex2', num: '603' },
        { clazz: 'experience', title: ' 1  2 ', name: 'ex3', num: '603' },
        { clazz: 'experience', title: ' 2  5 ', name: 'ex4', num: '603' },
        { clazz: 'experience', title: ' 5  10 ', name: 'ex5', num: '603' },
        { clazz: 'experience', title: ' 10 ', name: 'ex6', num: '603' }
    ]

    const experienceItem = experienceBlock.map(({ clazz, name, title, num }) => {
        return (
            <CustomCheckbox
                key={name}
                clazz={clazz}
                name={name}
                title={title}
                num={num} />
        )
    })

    return (
        <FilterBlock title=' ' clazz='experience'>
            {experienceItem}
            <FilterToggle clazz='experience'
                name='ex7'
                title=' ' />
        </FilterBlock>
    )
}

export default FilterExperience;