import FilterBlock from './../../common/filterWrapBlock/FilterWrapBlock';
import FilterRange from '../../common/filterRange/FilterRange';

const FilterAge = () => {
    return (
        <FilterBlock title='' clazz='age'>
            <FilterRange
                nameMax='max-age'
                nameMin='min-age'
                valueMax='40'
                valueMin='18'
                unit='' />
        </FilterBlock>
    )
}

export default FilterAge;